import React from 'react';
import Link from 'next/link';
import User from './User';
import Nav from './styles/NavStyle';

const NavBar = props => (
	<User>
		{({ data: { me } }) => (
			<Nav>
				<Link href="/items">
					<a>Marketplace</a>
				</Link>
				{me && (
					<Link href="/sell">
						<a>Upload</a>
					</Link>
				)}
				<Link href="/auth">
					<a>Sign In</a>
				</Link>
			</Nav>
		)}
	</User>
);

export default NavBar;
